<template>
  <div class="main-index">
    <div class="content">
      <img src="../../assets/bank-main-02.png" alt="">
      <div class="company-name">
        <el-input
          id="username"
          v-model="username"
          clearable
        ></el-input>
        <el-input
          id="userNum"
          style="margin-top: 33px;"
          v-model="userNum"
          clearable
        ></el-input>
        <el-input
          id="password"
          type="password"
          style="margin-top: 39px;"
          v-model="password"
          clearable
        ></el-input>
        <el-button
        style="margin-top: 45px;background-color: transparent;width: 265px;margin-left: -28px;border: none"
        type="primary"
        size="large"
        class="w-100p f-20 h-42"
        @click.native.prevent="handleLogin"
        ></el-button>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'nei_login',
  data () {
    return {
      username: '',
      userNum: '',
      password: ''
    }
  },
  mounted () {
    this.username = this.$store.state.userInfo.name
    this.userNum = this.$store.state.userInfo.number
  },
  methods: {
    handleLogin () {
      // 校验用户名密码
      if (this.username !== this.$store.state.userInfo.name) {
        this.$message.warning('用户名错误')
      } else {
        if (this.userNum !== this.$store.state.userInfo.number) {
          this.$message.warning('编号错误')
        } else {
          if (this.password !== this.$store.state.userInfo.password) {
            this.$message.warning('密码错误')
          } else {
            this.$router.push('/firstPage')
          }
        }
      }
    }
  }
}
</script>

<style lang="scss" scoped>
  .h-42 {
    height: 42px;
  }
  .main-index {
    width: 1200px;
    height: auto;
    margin: 0 auto;
    position: relative;
    // .navigation {
    //   width: 100%;
    //   height: 40px;
    //   background-color: #F1FBFC;
    //   padding: 0 35px;
    //   font-size: 14px;
    //   color: #666666;
    //   display: flex;
    //   align-items: center;
    //   justify-content: space-between;
    // }
    .content {
      position: relative;
      width: 100%;
      .company-name {
        position: absolute;
        right: 65px;
        top: 238px;
        width: 236px;
        height: 180px;
        font-size: 16px;
        /deep/ #username{
          height: 34px;
          border: none;
          margin-top: 1px;
        }

        /deep/ #userNum{
          height: 34px;
          border: none;
          margin-top: 2px;
        }

        /deep/ #password{
          height: 34px;
          border: none;
          margin-top: 2px;
        }
      }
    }
  }
</style>
